@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&family=Ruda&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0 ;
  padding: 0;
  Font-Family: 'Roboto Slab', Serif;
}
#wrapper {
  margin-left: 100px;
  margin-right: 100px;
  margin-top: 50px;
}
.logo{
  color: #f77f00;
  text-align: center;
  Font-Family: 'Ruda', Sans-Serif;
  font-weight: bold;
}


div.App{
  background-color: #14213d;
}
html {
  background-color: #14213d;
}

.card{
  margin-top: 20px;
}
.movie-card{
  text-align: center;
}
/* inside card body */
div.card-body{
  /* width: 15em; */
  background-color:#eae2b7;
}
.card-img{
  height: auto;
  width: auto;
  max-width: 100px;
  max-height: 300px;
}
ul {
  list-style: none;
}
button.btn.btn-primary:disabled{
  background-color: inherit;
  padding-top: 6px;
  border-radius: 5px;
  border-color: lightgrey;
  color: grey;
}
button.btn.btn-primary{
  background-color: #fcbf49;
  border-color: #495057;
  color: #495057;
}
button.btn.btn-info{
  background-color: #d62828;
  border-color: #495057;
  color: #000;
}

.alert-info{
  background-color: #fcbf49;
  border-color: #495057;
  color: #495057;

    text-align: center;
    margin: auto;
    margin-top: 2%;
  /* top: 50%;
  left: 50%; */
}
.movie-poster{
  display: -webkit-flex;
  display: flex;
}
div.card-title{
  margin-top: 10%;
  margin-right: 2%;
  margin-left: 2%;
  text-align: center;
  text-wrap: wrap;
}
div.card-body{
}
.wrapper{
  /* background-color: #FAF7ED; */
}
.nom-card .card-body{
  background-color: #fcbf49;
}
.nom-card .movie-card{
  background-color: black;
  position: relative;
  -webkit-animation: slide-nom 5s infinite;
          animation: slide-nom 5s infinite;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.nom-card {
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes slide-nom {
  from {left: -400px;}
  to {left: 0px;}
}

@keyframes slide-nom {
  from {left: -400px;}
  to {left: 0px;}
}
@-webkit-keyframes color-nom {
  from {background-color:#eae2b7;}
  to {background-color: #fcbf49;}
}
@keyframes color-nom {
  from {background-color:#eae2b7;}
  to {background-color: #fcbf49;}
}
